<script>
  export default {
    name: 'NavMenu',
    props: ['serviceType', 'serviceList', 'details'],
    data () {
      return {
      }
    },
    methods: {
//      
      onSubmit (id) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'success'
        })
        .then(() => {
          this.$emit('finishService', id)
        })
        .catch(() => {
          console.log('error')
        })
      },
//      
      onSubmitOff (id) {
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        })
        .then(() => {
          this.$emit('calloffService', id)
        })
        .catch(() => {
          console.log('error')
        })
      }
    }
  }
</script>
<style scoped>
  .infos{
    width: 100%;
    min-width: 742px;
    height: 758px;
    background: #fff;
    margin-top:30px;
    margin-left: 10px;
    position: relative;
  }

  .infos .bg{
    width: 100%;
    height: 130px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: url(../../assets/images/bgChange.png) no-repeat center;
    background-size:  100%;
  }

  .infos .top{
    padding-top: 30px;
    margin-left: 40px;
    color:#6d6d6d;
    margin-bottom: 20px;
  }

  .top::before{
    content:"";
    width:2px;
    height:20px;
    position:absolute;
    top:30px;
    left:30px;
    background:#4297da;
  }

  .infos .el-row{
    height: 40px;
    line-height: 40px;
  }

  .infos .btns{
    margin-top: 30px;
  }

  .infos .btns span{
    display: inline-block;
    color: #999999;
    font-size:12px;
  }

  .pingtai{
    margin-top: 20px;
  }

  .el-row .el-col:nth-child(1){
    color: #999999;
  }

  .el-row .el-col:nth-child(2){
    color: #666666;
  }
</style>



