<template>
  <div class="account">
    <p class="detail">{{userDetail}}</p>
    <el-form  :model="userParam" label-width="35%" :rules="rules" ref="ruleForm1">
      <el-form-item label="" prop="logonid">
        <el-input type="text" v-model="userParam.logonid" placeholder="" :maxlength="20" auto-complete="new-password" initial='off'></el-input>
      </el-form-item>
      <el-form-item label="" prop="pwd" v-if="this.$route.path === '/CreateAccount'">
        <input type="password" name="txtPassword" style="display:none">
        <el-input type="password" v-model="userParam.pwd" placeholder="" :maxlength="20" name="txtPassword" auto-complete="new-password" initial='off'></el-input>
      </el-form-item>

      <el-form-item label="" prop="position">
        <el-input type="text" v-model="userParam.position" placeholder="" :maxlength="20" auto-complete="new-password" initial='off'></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input type="text"  v-model="userParam.mobile" placeholder="" :maxlength="20" auto-complete="new-password" initial='off'></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input type="text" v-model="userParam.email" placeholder="" :maxlength="50" auto-complete="new-password" initial='off'></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="userParam.type" placeholder="" style="width:200px;">
          <el-option label="" value="1" selected></el-option>
          <el-option label="" value="2" selected></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="" prop="status">
        <el-select v-model="userParam.status" placeholder="" style="width:200px;">
          <el-option label="" value="0"></el-option>
          <el-option label="" value="1"></el-option>
        </el-select>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
  import globals from '././../../config'
  export default {
    props: ['userParam', 'userDetail'],
    data () {
      let validateAccount = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (globals.Reg.verifyAccount.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
      let validatePwd = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      let validatePosition = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        }
        callback()
      }
      let validateMobile = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (!globals.Reg.verifyMobile.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
      let validateEmail = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (!globals.Reg.verifyEmail.test(value)) {
          callback(new Error(''))
        }
        callback()
      }
      return {
        rules: {
          logonid: [
            { validator: validateAccount, trigger: 'blur' },
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: '20', trigger: 'blur'}
          ],
          pwd: [
            { validator: validatePwd, trigger: 'blur' },
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 20, message: '6-20', trigger: 'blur'}
          ],
          position: [
            { validator: validatePosition, trigger: 'blur' },
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: '20', trigger: 'blur'}
          ],
          mobile: [
            { validator: validateMobile, trigger: 'blur' },
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 20, message: '20', trigger: 'blur'}
          ],
          email: [
            { validator: validateEmail, trigger: 'blur' },
            {required: true, message: '', trigger: 'blur'},
            {min: 1, max: 50, message: '1-50', trigger: 'blur'}
          ]
        }
      }
    },
    created () {
    },
    methods: {
    }
  }
</script>
<style scoped>
  .el-input__inner:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 1000px white inset;
  }

  .account{
    position: relative;
  }

  .detail{
    position: absolute;
    top: 30px;
    left: 30px;
    border-left:3px solid #4297da;
    padding-left: 15px;
  }

  .el-input {
    width: 200px!important;
  }

  .el-form{
    padding-top: 50px;
  }
  .el-form-item{
    margin-top:10px;
  }

  .el-form-item__content{
    width: 50%;
    margin-left: 0;
  }
</style>



