<template>
  <div>
    <mainTop :pageBack="true" :Newprofile1="true" :title="''" :detail="''" :saves="true" @saveForm="saveForm"></mainTop>
    <instructionForm :ruleForm="ruleForm" :isDisabled="isDisabled" :formName="''"></instructionForm>
  </div>
</template>
<script>
  import globals from './../config'
  import mainTop from '../components/common/mainTop'
  import instructionForm from '../components/xxSetting/instructionForm'
  export default {
    data () {
      return {
        isDisabled: false,
        ruleForm: {
          name: '',
          url: '',
          status: '0',
          createdBy: ''
        }
      }
    },
    mounted () {
    },
    components: {
      mainTop,
      instructionForm
    },
    methods: {
      saveForm () {
        if (this.ruleForm.name.trim() === '') {
          this.$alert('', '')
          return
        }
        if (this.ruleForm.name.length > 20) {
          this.$alert('20', '')
          return
        }
        if (this.ruleForm.url.trim() === '') {
          this.$alert('', '')
          return
        }
        if (this.ruleForm.url.length > 200) {
          this.$alert('200', '')
          return
        }
        this.$confirm('', '', {}).then(() => {
          this.$http.post(globals.host + 'resource/addResourceQa', this.ruleForm).then((response) => {
            if (typeof (response.data) !== 'undefined' && response.data.code === 1) {
              this.$router.push({path: '/InstructionSetting'})
            } else {
              if (response.data.message && response.data.message.length > 0) {
                this.$alert(response.data.message, '')
              }
            }
          })
        })
      }
    }
  }
</script>
<style>

</style>



