<template>
  <div class="form">
    <div class="formName">{{formName}}</div>
    <div class="bg"></div>
    <div class="formList">
      <el-form :model="ruleForm" :rules="rules2" label-width="100px" class="demo-ruleForm">
        <el-form-item label="" prop="name">
          <el-input  v-model="ruleForm.name" :disabled="isDisabled" :maxlength="20"></el-input>
        </el-form-item>
        <el-form-item label="code" prop="code">
          <el-input  v-model="ruleForm.code" :disabled="isDisabled" :maxlength="6"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type" >
          <el-select v-model="ruleForm.type" placeholder="" :disabled="isDisabled">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="2"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="pid" v-show="ruleForm.type == 2" required>
          <el-select v-model="ruleForm.pid" placeholder=""  :disabled="isDisabled">
            <el-option v-for="data in getFirstList" key="getFirstList" :label="data.name" :value="data.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" >
          <el-select v-model="ruleForm.status" placeholder="" >
            <el-option label="" value="0" ></el-option>
            <el-option label="" value="1" ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
  import globals from '../../config'
  export default {
    props: ['isDisabled', 'ruleForm', 'getFirstList', 'formName'],
    data () {
      let filterCode = (rule, value, callback) => {
        console.log(value)
        if (!globals.Reg.verifyNumber.test(value.trim())) {
          callback(new Error('code'))
        } else {
          callback()
        }
      }
      return {
        rules2: {
          name: [
            { required: true, message: '', trigger: 'blur' },
            { min: 1, max: 20, message: '20', trigger: 'blur' }
          ],
          code: [
            { required: true, message: 'code', trigger: 'blur' },
            { min: 6, max: 6, message: 'code6', trigger: 'blur' },
            { validator: filterCode, message: 'code', trigger: 'blur' }
          ],
          type: [
            { required: true, trigger: 'change', message: '' }
          ]
        }
      }
    },
    mounted () {
    },
    components: {
    },
    methods: {
    }
  }
</script>
<style>
  .form{
    background-color: #fff;
    padding-top: 30px;
    margin-top: 20px;
    min-height: 623px;
    position: relative;
  }
  .form .bg{
    width: 100%;
    height: 130px;
    position: absolute;
    bottom: 0;
    left: 0;
    background: url(../../assets/images/bgChange.png) no-repeat center;
    background-size:  100%;
  }
  .form .formName{
    margin: 0px 30px;
    border-left: 3px solid skyblue;
    padding-left: 10px;
  }
  .form .formList{
    width:400px;
    margin: 50px auto;
  }
</style>



